<template>

    <div class="mb-4">
        <h1>Quotes Added</h1>
        <div class="progress">
            <span :style="{width: percentage}" class="progress-bar">{{currentAmount}}/{{maxAmount}}</span>
        </div>
    </div>
</template>


<script>
export default {
    props: ['currentAmount', 'maxAmount'],
    data: function(){
        return {

        }
    },
    computed: {
        percentage(){
            return this.currentAmount / this.maxAmount * 100 + '%'; 
        }
    }
}
</script>


<style  scoped>
    span {
        display: inline-blocks;
        background: #2bb2bb;
    }

    div {
        font-family: 'Josefin Sans', sans-serif;
    }
</style>