<template>
  <div id="app" class="container">
    <quote-count :maxAmount="maximum" :currentAmount="quotes.length"></quote-count>
    
    <quote-add @addQuote="addQuote"></quote-add>
   
    <div id="card-container"></div>
    <div class="row mb-4">
      <quote-card :key="index" v-for="(quote, index) in quotes" :quoteContent="quote" @quoteDeletion="deleteQuote(index)"></quote-card>
     </div>
    <reminder>
      <span>Info: click on a quote to delete it.</span>
    </reminder>
  </div>
</template>

<script>
import QuoteCount from './components/QuoteCount.vue';
import QuoteAdd from './components/QuoteAdd.vue';
import QuoteCard from './components/QuoteCard';
import reminder from './components/reminder';

export default {
  name: 'App',
  components: {
    QuoteCount, QuoteAdd, QuoteCard, reminder
  },
  data: function() {
    return {
      quotes: ['Let us sacrifice our today so that our children can have a better tomorrow.'],
      maximum: 10
    }
  },
  methods: {
    deleteQuote(index){
      this.quotes.splice(index, 1);
    }, 
    addQuote(e){
      if (this.quotes.length >= this.maximum) return alert('please delete a quote before adding new one');
      this.quotes.push(e);
    }
  }
}
</script>

<style>
  .container {
    margin-top: 20px
  }
</style>
