<template>
    
    <div @click="deleteQuote" class="col-sm-6 col-md-4 col-lg-3 ">
       <div class="alert alert-success">
            <p>{{quoteContent}}</p>
       </div>
      
    </div>
</template>


<script>
export default {
    props: ['quoteContent'],
    methods: {
        deleteQuote(){
            this.$emit('quoteDeletion')
        }
    }
    
}
</script>


<style  scoped>
    p {
        font-family: 'Ruslan Display', cursive;
    }

    .alert:hover {
        background: #ff847c;
        cursor: pointer;
    }
</style>