<template>

    <div class="mb-4 container">
        <h2 class="center-align">Quote</h2>
        <div  class="center-align">
            <textarea  ref="quoteInput" name="" id="" cols="60" rows="5" v-model="quote"></textarea>
        </div>
        
        <div class="center-align">
              <button @click="addQuote" class="btn btn-success">Add Quote</button>
        </div>
      
    </div>
</template>


<script>
export default {

    data: function(){
        return {
            quote:''
        }
    },
    computed: {
      
    },
    methods: {
        addQuote(){
            this.$emit('addQuote', this.quote); 
            this.quote = '';
        }
    }
}
</script>


<style  scoped>
   .container {
       justify-content: center;
   }

   .center-align {
       text-align: center
   }

   div {
         font-family: 'Josefin Sans', sans-serif;
   }
</style>